import { useCallback, useEffect, useRef, useState } from "react";
// components
import Container from "components/admin/common/Container";
import List from "components/admin/common/List";
import Name from "components/admin/common/Name";
import Order from "components/admin/common/Order";
import Ulist from "components/admin/common/Ulist";
import Title from "components/admin/Title";

// modal
import MWrapper from "components/admin/modal/MWrapper";
import MContainer from "components/admin/modal/MContainer";
import MExitButton from "components/admin/modal/MExitButton";
import MTitle from "components/admin/modal/MTitle";
import MSubTitle from "components/admin/modal/MSubTitle";
import MSubWrapper from "components/admin/modal/MSubWrapper";
import MText from "components/admin/modal/MText";
import MContent from "components/admin/modal/MContent";
// controllers
import { getQuestion } from "controllers/admin";
// pages
import Loading from "pages/LoadingPage";
import File from "components/admin/modal/File";



const Quest = () => {

  const [open, setOpen] = useState<boolean>(false);
  const [num, setNum] = useState<number>(0);
  const [result, setResult] = useState<any | any[]>([]);
  const [item, setItem] = useState<any | any[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const ref = useRef<HTMLDivElement>(null);

  const fetchMoreData = async() => {
    setIsLoading(true);
    setResult(result.concat(item.slice(0,20)));
    setItem(item.slice(20));
    setIsLoading(false);
  }

  const onMouseDown = (e:any) => {
    if(!ref.current) return ;

    if(!e.path.includes(ref.current)) {
      setOpen(false);
    }
  }

  useEffect(() => {
    if(open) {
      window.addEventListener('mousedown', onMouseDown, true);
    }
    return () => {
      window.removeEventListener("mousedown", onMouseDown, true);
    }
  },[open]);


  const _infiniteScroll = useCallback(() => {

    let scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);

    let scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);

    let clientHeight = document.documentElement.clientHeight;
    scrollHeight-=100;
    if(scrollTop + clientHeight >= scrollHeight && isLoading === false) {  
      fetchMoreData();
    }
  },[isLoading]);

  const getFetchData = async() => {
    let response;
    response = await getQuestion();
    if(response) {
      setResult(response.slice(0,20));
      response = response.slice(20);
      setItem(response);
    }
    setIsLoading(false);
  }

  useEffect(() => {
    getFetchData();
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', _infiniteScroll, true);
    return () => window.removeEventListener('scroll', _infiniteScroll, true);
  }, [_infiniteScroll]);

  return (
    <>
      <Title>문의내역</Title>
      {
        isLoading ? <Loading />
                  : (
                    <Container>
                      <Ulist>
                        {
                          result.map((res:any, idx:number) => (
                            <List key={idx} onClick={() => {
                              setOpen(true);
                              setNum(idx);
                            }}>
                              <Order>{idx+1}</Order>
                              <Name>{res.title}</Name>
                            </List>
                          ))
                        }
                      </Ulist>
                    </Container>
                  )
      }
      {
        open && (
          <MWrapper>
          <MContainer ref={ref}>
            <MExitButton src="/images/icons/Exit.png" alt="닫기" onClick={() => setOpen(false)}></MExitButton>
            <MTitle>{result[num].title}</MTitle>
            <MSubTitle>이메일</MSubTitle>
            <MContent>{result[num].email}</MContent>
            <MSubWrapper>
              <MSubTitle>내용</MSubTitle>
              <MSubTitle>{result[num].date.substring(0, 10)}</MSubTitle>
            </MSubWrapper>
            <MText>{result[num].text}</MText>
            <MTitle>첨부파일</MTitle>
            {
             result[num].file !== undefined && result[num].file.file.map((res:any, idx:number) => (
                <File key={res.path} filename={res.originalname} />
              ))
            }
          </MContainer>
        </MWrapper>
        )
      }
    </>
  )
}

export default Quest;