import MWrapper from "components/admin/modal/MWrapper";
import Wrapper from "components/auth/mypage/basic/Wrapper";
import BtnContainer from "components/auth/mypage/quest/BtnContainer";
import Button from "components/auth/mypage/quest/Button";
import FileBox from "components/auth/mypage/quest/FileBox";
import FileUploadBtn from "components/auth/mypage/quest/FileUploadBtn";
import Input from "components/auth/mypage/quest/Input";
import Part from "components/auth/mypage/quest/Part"
import TextArea from "components/auth/mypage/quest/TextArea";
import Title from "components/auth/mypage/quest/Title";

import { requestUs } from "controllers/quest";
import { useState } from "react";
import QuestList from "./QuestList";

const Quest = () => {
  // select part
  const [open, setOpen] = useState<IQuest>({
    question : true,
    questionList : false,
  });
  // 문의내용
  const [text, setText] = useState<string>('');
  // 파일
  const [file, setFile] = useState<File[]>([]);
  // 제목
  const [title, setTitle] = useState<string>('');
  // 답변받을 이메일
  const [email, setEmail] = useState<string>('');

  const onChange = (e:any) => {

    if(e.target.files.length > 3) {
      alert("파일은 최대 3개까지만 업로드 가능합니다.");
      e.preventDefault();
    }
    else {
      setFile(e.target.files);
    }
  }

  const onSubmit = async() => {
    const formData = new FormData();
    let i;
    formData.append('text', text);
    formData.append('email',email);
    formData.append('title',title);
    for( i=0; i<file.length; i++ ){
      if(i>2) return ;
      else formData.append('file',file[i]);
    } 

    await requestUs(formData);
    initialize();
  }

  const initialize = () => {
    setTitle('');
    setText('');
    setEmail('');
    setFile([]);
  }

  return (
    <>
    <Wrapper>
      <Part open={open.question} onClick={() => setOpen({question:true, questionList: false})}>
        제목
      </Part>
      <Part open={open.questionList} onClick={() => setOpen({question:false, questionList: true})}>
        문의내역
      </Part>
    </Wrapper>
      {
        open.question ? (
          <Wrapper>
            <Input onChange={(e) => setTitle(e.target.value)} value={title} placeholder="직접 입력" />
            <Title>이메일</Title>
            <Input onChange={(e) => setEmail(e.target.value)} value={email} placeholder="직접 입력" />
            <Title>문의내용</Title>
            <TextArea placeholder="파일은 최대 3개까지 업로드 가능합니다." onChange={(e:any) => setText(e.target.value)} value={text}/>
            <Title>첨부파일</Title>
            {
              file && Array.from(file).map(data => (
                <FileBox key={data.lastModified}>{data.name}</FileBox>
              ))
            }
            <BtnContainer>
              <FileUploadBtn  onChange={onChange} accept="image/png, image/jpeg, image/jpg, application/pdf" type="file" id="file" multiple={true} max-length="3"/>
              <Button>
                <label htmlFor="file">파일첨부</label>
              </Button>
              <Button onClick={() => onSubmit()}>글 올리기</Button>
            </BtnContainer>
          </Wrapper>
        ) : (
          <QuestList />
        )
      }
    </>
  )
}

interface IQuest {
  question : boolean;
  questionList : boolean;
}

export default Quest;