import { useRef, useState } from "react";
// component
import Button from "components/auth/signin/Button";
import Container from "components/auth/signin/Container";
import Input from "components/auth/signup/Input";
import Title from "components/auth/signin/Title";
import CheckContainer from "components/auth/signup/CheckContainer";
import PromiseList from "components/auth/signup/PromiseList";
import CheckBox from "components/auth/signup/CheckBox";
import CheckLabel from "components/auth/signup/CheckLabel";
import Navgation from "components/auth/signup/Navgation";
import Error from "components/auth/signup/Error";
import WhiteSpace from "components/auth/signup/WhiteSpace";
// controllers
import { idDuplicateCheck, signUpUser } from "controllers/auth";
// lib
import { validationIdCheck, validationPwdCheck } from "lib/utils/authCheck";
import {ICheck, IUser} from 'lib/interfaces/auth';
import Watch from "components/auth/signup/Watch";
import Service from "containers/auth/Service";
import PersonalInformation from "containers/auth/PersonalInformation";

export const SignUp = () => { 

  const [id, setId] = useState<IUser>({
    error : true,
    value : '',
    active : false
  });

  const [pwd, setPwd] = useState<IUser>({
    error : true,
    value : '',
    active : false
  });

  const [configPwd, setConfigPwd] = useState<IUser>({
    error : true,
    value : '',
    active : false
  });

  const [check, setCheck] = useState<ICheck>({
    box1 : false,
    box2 : false,
    box3 : false,
    all : false,
    error: true,
  });

  const [modal, setModal] = useState<IModal>({
    service: false,
    personal: false
  });

  const idRef = useRef<HTMLInputElement>(null);
  const pwdRef = useRef<HTMLInputElement>(null);
  const checkRef = useRef<HTMLDivElement>(null);
  
  const idCheck = (e:any) => {
    let res;
    if(e.target.value.length > 3) {
      res = validationIdCheck(e.target.value);
      setId({
        ...id, 
        error : res,
        value : e.target.value
      });
    }
  }

  const duplicateCheck = async(user_id:string) => {
    let res = await idDuplicateCheck(user_id);
    let val = validationIdCheck(user_id);

    setId({
      ...id,
      error: res&&val
    })
  }

  const pwdCheck = (e:any) => {
    let res;
    if(e.target.value.length > 7) {
      res = validationPwdCheck(e.target.value);
      setPwd({
        ...pwd, 
        error : res,
        value : e.target.value
      });
    }
  }

  const pwdBlurCheck = (e:any) => {
    let res;
    res = validationPwdCheck(e.target.value);
      setPwd({
        ...pwd, 
        error : res,
        value : e.target.value
      });
      if(e.target.value.length < 8) {
        setPwd({
          ...pwd,
          error: false,
          value: e.target.value
        })
      }
    
  }

  const configPwdCheck = (e:any) => {
    
    if(e.target.value !== pwd.value) setConfigPwd({...configPwd, error : false});
    else setConfigPwd({...configPwd, error: true});
  }

  const allCheck = () => {
    setCheck({
      box1 : !check.all,
      box2 : !check.all,
      box3 : !check.all,
      all : !check.all,
      error : true,
    });
  }
  
  const submitSignUp = () => {
    if(id.error && pwd.error && configPwd.error &&check.box1 && check.box2) {
      signUpUser(id.value, pwd.value, check.box3);
      // window.location.href="/";
      alert("회원가입을 축하합니다.");
    } else {
      if(!id.error) {
        idRef.current && idRef.current.focus();
        return ;
      }
      if(!pwd.error) {
        pwdRef.current && pwdRef.current.focus();
        return ;
      }
      if(!check.box1 || !check.box2) {
        setCheck({...check, error:false});
        checkRef.current && checkRef.current.focus();
        return ;
      }
    }
  }

  return (
    <Container>
      <Title>
        회원가입을 통해 3355LAB으로<br />
        더 많은 서비스를 경험해보세요.
      </Title>
      <Navgation>
        아이디
      </Navgation>
      <Input type="text"
              placeholder="아이디 입력 ( 영문, 숫자 가능 )"
              onChange={idCheck}
              ref={idRef}
              max-length="20"
              error={id.error}
              onBlur={(e:any) => duplicateCheck(e.target.value)}
      />
      {
        !id.error && <Error>아이디를 확인해주세요.</Error>
      }
      <WhiteSpace />
      <Navgation>
        비밀번호
      </Navgation>
      <Input type="password"
            placeholder="비밀번호 ( 영문, 숫자, 특수문자 포함 8자 이상 )"
            onChange={pwdCheck}
            ref={pwdRef}
            max-length="20"
            error={pwd.error}
            onBlur={(e:any) => pwdBlurCheck(e)}
      />
      {
        !pwd.error && <Error>비밀번호를 확인해주세요.</Error>
      }
      <Input type="password"
              placeholder="비밀번호확인"
              onChange={configPwdCheck}
              error={configPwd.error}
      />
      {
        !configPwd.error && <Error>비밀번호와 일치하는 지 확인해주세요.</Error>
      }
      <CheckContainer ref={checkRef}
                      onClick={() => allCheck()}
                      error={check.error}
                      >
        전체 동의하기
      </CheckContainer>
      {
        !check.error && <Error>(필수)약관동의를 확인해주세요.</Error>
      }
      <PromiseList>
        <CheckBox id="agree1" type="checkbox" checked={check.box1} onChange={() => setCheck({...check, box1 : !check.box1})} />
        <CheckLabel onClick={() => setCheck({...check, box1: !check.box1})}>
          (필수) 이용약관 동의하기
        </CheckLabel>
        <Watch onClick={() => setModal({...modal, service: true})}>보기</Watch>
        {
          modal.service && <Service open={modal.service} onClick={() => setModal({...modal, service: !modal.service})}/>
        }
      </PromiseList>
      <PromiseList>
      <CheckBox id="agree2" type="checkbox" checked={check.box2} onChange={() => setCheck({...check, box2 : !check.box2})} />
        <CheckLabel onClick={() => setCheck({...check, box2: !check.box2})}>
          (필수) 개인정보 처리방침 동의하기
        </CheckLabel>
        <Watch onClick={() => setModal({...modal, personal: true})}>보기</Watch>
        {
          modal.personal && <PersonalInformation open={modal.personal} onClick={() => setModal({...modal, personal: !modal.personal})}/>
        }
      </PromiseList>
      <PromiseList>
        <CheckBox id="agree3" type="checkbox" checked={check.box3} onChange={() => setCheck({...check, box3 : !check.box3})} />
        <CheckLabel onClick={() => setCheck({...check, box3: !check.box3})}>
          (선택) 제3자 정보제공 동의하기
        </CheckLabel>
      </PromiseList>
      <Button onClick={() => submitSignUp()}>가입완료</Button>
    </Container>
  )
};

interface IModal {
  service: boolean;
  personal: boolean;
}

export default SignUp;