import Fetch from "./fetch"

/**
 @param { none }
 @version 1
 */
export const getUserBookmark = () => {
  return Fetch("/api/v1/user/bookmark", "GET")
    .then(res => {
      if(res.success) {
        return res.results;
      }
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**
 @param { bookmark : 북마크한 회사 id}
 @version 1
 */
export const cancleBookmark = (bookmark:string) => {
  const sendData = {
    bookmark
  }
  return Fetch("/api/v1/user/bookmark","DELETE", sendData)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

/**
 @param { bookmark : 북마크한 회사 id}
 @version 1
 */

export const registerBookmark = (bookmark:string) => {
  const sendData = {
    bookmark
  }

  return Fetch("/api/v1/user/bookmark","POST",sendData)
    .then(res => {
      return res.success;
    })
    .catch(err => {
      return Promise.reject(err);
    })

}